import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
   CircularProgress, Grid, Button, Alert
  } from '@mui/material'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import AddCommentIcon from '@mui/icons-material/AddComment';
import FavoriteIcon from '@mui/icons-material/Favorite';
import Comment from "./Comment";
import CommentList from "./CommentList";

const BlogDetails = ({setUser}) => {

    const [blog, setBlog] = useState(null)
    const [active, setActive] = useState(false)
    const [commentView, setCommentView] = useState(false)
    const [comments, setComments] = useState(null)
    const [successMsg, setSuccessMsg] = useState(null)
    const [errorMsg, setErrorMsg] = useState(null)
    const [likes, ssetLikes] = useState(null)
    const { id } = useParams()

    useEffect(() => {
        if(!blog){
            axios.get(`http://localhost:3001/blogs/${id}`)
            .then((result) => {
                console.log('result.data here is', result.data)
                setBlog(result.data)
            })
            .catch((err) => {
                console.log('error here is ', err)
            })
        }
    },[])

    useEffect(() => {
        if(!comments){
            axios.get(`http://localhost:3001/comments/${id}`)
            .then((result) => {
                console.log(`comments over here is ${result.data[0].comments[0]}`)
                setComments(result.data[0].comments)
            })
        }
    },[comments])

    useEffect(() => {
        const loggedUserJSON = window.localStorage.getItem('loggedBlogUser')
        console.log(`logged user here is ${JSON.parse(loggedUserJSON)}`)
        if(loggedUserJSON) {
            const blogUser = JSON.parse(loggedUserJSON)
            setUser(blogUser)
        }
    },[])

    const handleLike = async () => {
        try {
            setActive(true)
            const updatedBlog = await axios.patch(`http://localhost:3001/update/${id}`,{likes: blog.likes + 1})
            setBlog(updatedBlog.data)
            setSuccessMsg('Liked blog!')
            setTimeout(() => {
                setSuccessMsg(null)
            }, 3000)
            
        }
        catch(err) {
            console.log(`Error: ${err}`)
            setErrorMsg('Error unliking blog. Try again later.')
            setTimeout(() => {
                setErrorMsg(null)
            }, 3000)
        }
        
    }

    const handleUnLike = async () => {
        try {
            setActive(false)
            const updatedBlog = await axios.patch(`http://localhost:3001/update/${id}`,{likes: blog.likes - 1})
            setBlog(updatedBlog.data)
            setSuccessMsg('Unliked blog!')
            setTimeout(() => {
                setSuccessMsg(null)
            }, 3000)
        }
        catch(err) {
            console.log(`Error: ${err}`)
            setErrorMsg('Error unliking blog. Try again later.')
            setTimeout(() => {
                setErrorMsg(null)
            }, 3000)
        }
        
    }
    return ( 
        <div>
            <Grid container justify-content='center' alignItems='center' direction='column' pl={10} pt={10}>
            {!blog && <CircularProgress />}
            {successMsg && <Alert severity="success">{successMsg}</Alert>}
            {errorMsg && <Alert ml={-6} mt={-2} severity="error">{errorMsg}</Alert>}
            {blog && <div>
                {!active ? <Button sx={{mb: 2}} variant='outlined' onClick={handleLike}startIcon={<FavoriteBorderIcon />}> LIKES {blog.likes} </Button> :                
                 <Button sx={{mb: 2}} variant='outlined' onClick={handleUnLike}startIcon={<FavoriteIcon />}> LIKED {blog.likes}</Button>}
                <h1>{blog.title}</h1>
                <h2>By: {blog.author}</h2>
                <p>{blog.content}</p>
                {!commentView && <Button sx={{mt: 2}} variant='outlined' onClick={() => setCommentView(true)}startIcon={<AddCommentIcon />}> ADD COMMENT</Button>}
                {commentView && <Comment setSuccessMsg={setSuccessMsg} setErrorMsg={setErrorMsg} setComments={setComments}/>}
            </div>}
            {comments && <CommentList comments={comments}/>}
            </Grid>
        </div>
     );
}
 
export default BlogDetails;