import axios from "axios";
import { useEffect } from "react";
import { useParams } from "react-router-dom";

const Verified = () => {
    const { confirmationCode } = useParams()
    useEffect(() => {
        try {
            axios.get(`http://localhost:3001/confirm/${confirmationCode}`)
        }
        catch(err) {
            console.log(err)
        }
    },[])
    return ( 
        <div>
            <h1>Thank you! Your account is now verified</h1>
        </div>
     );
}
 
export default Verified;