import {
    TextField, Grid, Button, Alert, InputAdornment, IconButton
  } from '@mui/material'
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import StraightIcon from '@mui/icons-material/Straight';
import { useState } from 'react';
import { width } from '@mui/system';
import axios from 'axios';
import { Link } from 'react-router-dom'

const SignUp = () => {
    const [email, setEmail] = useState('')
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false)

    const handlePasswordVisibility = () => {
        setShowPassword(password => !password)
        console.log(`password here is ${password}`)

    }
    const handleSignUp = async (e) => {
        console.log('clicked')
        console.log(`password here is ${password}`)
        e.preventDefault()
        await axios.post('http://localhost:3001/sign-up',{email,username,password})
        console.log('clicked sign up')
    }
    return ( 
        <div>
            <form onSubmit={handleSignUp}>
            <Grid container justify-content='center' alignItems='center' direction="column" gap={5} p={6}>
                    <h2>Sign up</h2>
                    <TextField label='Email' value={email} onChange={(e) => setEmail(e.target.value)}/>
                    <TextField label='Username' value={username} onChange={(e) => setUsername(e.target.value)} />
                    <TextField sx={{width: 200}}label='Password' value={password} type={!showPassword ? 'password' : 'text'} onChange={(e) => setPassword(e.target.value)} 
                    InputProps={{endAdornment: <InputAdornment position='end'>
                    <IconButton onClick={handlePasswordVisibility} edge='end'>{!showPassword ? <VisibilityIcon /> : <VisibilityOffIcon />}</IconButton></InputAdornment>}}/>
                    <Button variant="contained" endIcon={<StraightIcon/>} type='submit'>Sign Up</Button>
                    <div>
                        Already have an account? <Link to='/login'>login here</Link>
                    </div>

            </Grid>
            </form>
        </div>
     );
}
 
export default SignUp;