import { useEffect, useState } from "react";
import axios from "axios";
import {
    TextField, Grid, Box, Button, Alert, List, ListItem, ListItemText,CircularProgress
  } from '@mui/material'
import { Link } from 'react-router-dom'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import { borderColor } from "@mui/system";



const BlogList = ({user, setUser}) => {
    const [blogs, setBlogs] = useState([])

    useEffect(() => {
        axios.get('http://localhost:3001/blogs')
            .then((result) => {
                console.log('blogList here is', result.data);
                setBlogs(result.data)
            })
    },[])

    useEffect(() => {
        const loggedUserJSON = window.localStorage.getItem('loggedBlogUser')
        console.log(`logged user here is ${JSON.parse(loggedUserJSON)}`)
        if(loggedUserJSON) {
            const blogUser = JSON.parse(loggedUserJSON)
            setUser(blogUser)
        }
    },[])

    return ( 
        <div>
            {!blogs && <CircularProgress />}
            {blogs && <div>
            <Grid container justify-content='center' alignItems='center' direction='column' pl={10}>
                <h2>All Blogs</h2>
            </Grid>
            {blogs.map((blog) => 
                <div>
                    <Grid container justify-content='center' alignItems='center' direction='column' pl={0}>
                    <List >
                        <Link to={`/blogs/${blog._id}`} style={{textDecoration:'none' ,color: 'black'}}>
                        <ListItem>
                            <Box ml={10}>
                                <ListItemText 
                                    primary={blog.title}
                                    secondary={
                                    <div>
                                        <div>{blog.author}</div>
                                        <div>{blog.content}</div>
                                    </div>}
                                />
                            </Box>
                        </ListItem>
                        </Link>
                    </List>
                   
                    </Grid>
                </div>)}
                <Grid container justify-content='center' alignItems='center' direction='column' pl={10}>
                <Link to='/create-blog'><Button variant="contained" endIcon={<ArrowUpwardIcon/>}>Create Blog</Button></Link>
            </Grid>
            </div>}
        </div>
     );
}
 
export default BlogList;