import {
    TextField, Grid, Button, Alert, InputAdornment, IconButton
  } from '@mui/material'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { useState } from 'react';
import LoginIcon from '@mui/icons-material/Login';
import axios from 'axios';
import { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom'

const Login = ({user, setUser}) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('')
    const [showPassword, setShowPassword] = useState(false)
    const [errorMsg, setErrMsg] = useState(null)
    const [successMsg, setSuccessMsg] = useState(null)
    const navigate = useNavigate();

    const handlePasswordVisibility = () => {
        setShowPassword(password => !password)
        console.log(`password here is ${password}`)
    }

    const handleLogin = async (e) => {
        e.preventDefault()
        
        try {
            await axios.post('http://localhost:3001/login', {username,password})
                .then((result) => {
                    console.log(`result.data here is`, result.data)
                    setUser(result.data)
                    window.localStorage.setItem('loggedBlogUser', JSON.stringify(result.data))
                    console.log(window.localStorage)
                })
            console.log('succesfully logged in')
            setSuccessMsg('Successfully logged in :)')
            setTimeout(() => {
                navigate('/blogs')
            },1500)
        }
        catch(err) {
            setErrMsg(err.message)
        }
        

    }
    return ( 
        <div>
            <form onSubmit={handleLogin}>
            <Grid container justify-content='center' alignItems='center' direction="column" gap={5} p={6}>
                    {successMsg && <Alert severity="success">{successMsg}</Alert>}
                    {errorMsg && <Alert severity="error">{errorMsg}</Alert>}
                    <h2>Sign in to your account</h2>
                    <TextField label='Username' value={username} onChange={(e) => setUsername(e.target.value)}/>
                    <TextField sx={{width: 200}}label='Password' value={password} type={!showPassword ? 'password' : 'text'} onChange={(e) => setPassword(e.target.value)} 
                    InputProps={{endAdornment: <InputAdornment position='end'>
                    <IconButton onClick={handlePasswordVisibility} edge='end'>{!showPassword ? <VisibilityIcon /> : <VisibilityOffIcon />}</IconButton></InputAdornment>}}/>
                    <Button variant="contained" endIcon={<LoginIcon/>} type='submit'>Login</Button>
                    <div>
                        Don't have an account? <Link to='/sign-up'>sign up here</Link>
                    </div>
            </Grid>
            </form>
        </div>
     );
}
 
export default Login;