import {
    TextField, Grid, Button, Alert
  } from '@mui/material'
import AddCommentIcon from '@mui/icons-material/AddComment';
import { useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';

const Comment = ({setComments,setSuccessMsg, setErrorMsg}) => {
    const [comment, setComment] = useState('')
    const { id } = useParams()

    const addComment = async () => {
        try {
            const newComment = await axios.patch(`http://localhost:3001/add-comment/${id}`, {comment})
            console.log(`newComment here is ${JSON.stringify(newComment.data)}`)
            setComments(newComment.data.comments)
            setComment('')
            setSuccessMsg('Added a comment!')
            setTimeout(() => {
                setSuccessMsg(null)
            }, 3000)
        }
        catch(err){
            console.log(`Error: ${err}`)
            setErrorMsg('Error adding a comment. Try again later.')
            setTimeout(() => {
                setErrorMsg(null)
            }, 3000)
        }
        
    }
    return ( 
        <div>
            <Grid container justify-content='center'  direction="column" gap={5} p={6} ml={-6} mt={-2}>
                    <TextField label='Comment' multiline value={comment} onChange={(e) => setComment(e.target.value)} InputProps={{endAdornment: <AddCommentIcon style={{cursor: 'pointer'}}onClick={addComment}/>}}/>
            </Grid>
        </div>
     );
}
 
export default Comment;