import {
    TextField, Grid, Button, Alert
  } from '@mui/material'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import SendIcon from '@mui/icons-material/Send';
import { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom';

const BlogForm = ({user}) => {
    const [blogTitle, setBlogTitle] = useState('');
    const [blogAuthor, setBlogAuthor] = useState('');
    const [blogContent, setBlogContent] = useState('');
    const [errorMsg, setError] = useState(null)
    const [successMsg, setSuccess] = useState(null)
    const navigate = useNavigate()

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(`New Blog added:`)
        console.log(`Blog Title: ${blogTitle}`)
        console.log(`Blog Author: ${blogAuthor}`)
        console.log(`Blog Content: ${blogContent}`)
        const blogToSave = {
            title: blogTitle,
            author: blogAuthor,
            content: blogContent,
            likes: 0,
            peopleWhoLiked: null,
            userId: user.id
        }
        axios.post('http://localhost:3001/', blogToSave, {headers: {Authorization: `Bearer ${user.token}`}})
            .then(() => {
                console.log('Saved to db!')
                setSuccess('Blog successfully saved :)')
                setTimeout(() => {
                    setSuccess(null)
                    navigate('/blogs')
                },4000)
            })
            .catch((err) => {
                console.log('Error saving to db: ', err)
                setError('Error saving blog. Try again later.')
                setTimeout(() => {
                    setError(null)
                },4000)
            })

        setBlogTitle('')
        setBlogAuthor('')
        setBlogContent('')
    }


    return ( 
        <div>
            <form onSubmit={handleSubmit}>
            <Grid container justify-content='center' alignItems='center' direction="column" gap={5} p={6}>
                    {successMsg && <Alert severity="success">{successMsg}</Alert>}
                    {errorMsg && <Alert severity="error">{errorMsg}</Alert>}
                    <h2>Create new blog</h2>
                    <TextField label='Blog Title' value={blogTitle} onChange={(e) => setBlogTitle(e.target.value)}/>
                    <TextField label='Blog Author' value={blogAuthor} onChange={(e) => setBlogAuthor(e.target.value)}/>
                    <TextField label='Blog Content' rows={10} multiline value={blogContent} onChange={(e) => setBlogContent(e.target.value)}/>
                    <Button variant="contained" endIcon={<ArrowUpwardIcon/>} type='submit'>Add new blog</Button>
                    <Link to='/blogs' style={{textDecoration: 'none'}}><Button  variant="outlined" endIcon={<SendIcon/>}>View all blogs</Button></Link>
            </Grid>
            </form>
        </div>
     );
}
 
export default BlogForm;