import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import DriveFileRenameOutlineIcon from '@mui/icons-material/DriveFileRenameOutline';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
// import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import { Link } from 'react-router-dom'

const Navbar = ({user,setUser}) => {
    const handleLogout = () => {
        window.localStorage.removeItem('loggedBlogUser')
        setUser(null)
    }
    return ( 
        <div className="navbar-container">
            {user && <div className="logged-in-container">
                <h2>signed in as {user.username}</h2>
                <Link to='/'><button onClick={handleLogout} className='logout-button'>LOGOUT</button></Link>
                <Link to='/blogs'><button className='home-button'>ALL BLOGS</button></Link>
            </div>}
            {!user && <div className="logged-out-container">
                <h2>not signed in</h2>
                <Link to='/login'><button className='logout-button'>LOGIN</button></Link>
            </div>}
        </div>
     );
}
 
export default Navbar;